"use client";
import { useState } from "react";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Chrome } from "lucide-react";

export function AuthSignupForm() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassWord, setConfirmPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if(password !==confirmPassWord){
      alert("Passwords do not match");
      return;
    }

    setIsLoading(true)

    await new Promise ((resolve)=>setTimeout(resolve,1000))
    setIsLoading(false)
     console.log('Signup attempt:', { name, email, password })
  };

  const handleGoogleSignIn = () => {
    console.log('Google sign-in clicked')
  }


  return (
    <div className="space-y-6">
      <form  onSubmit={handleSubmit} className="space-y-4">
        <div className="space-y-2 flex-col flex gap-1">

          <Label className="text-sm font-medium text-foreground/90" htmlFor="name">Full Name
          </Label>

          <Input
            id="name"
            type="text"
            placeholder="John Doe"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="bg-white/5 border-white/10 placeholder:text-foreground/40 focus:border-white/20 focus:bg-white/10"
          />
        </div>

        <div className="space-y-2 flex-col flex gap-1">
          <Label htmlFor="signup-email"> Email</Label>
          <Input
            id="signup-email"
            type="email"
            placeholder="you@example.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="bg-white/5 border-white/10 placeholder:text-foreground/40 focus:border-white/20   focus:bg-white/10"
          />
        </div>

        <div className="space-y-2  flex-col flex gap-1">
          <Label htmlFor="signup-password"> Password</Label>
          <Input
            id="signup-password"
            type="password"
            placeholder="********"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="bg-white/5 border-white/10 placeholder:text-foreground/40 focus:border-white/20   focus:bg-white/10"
          />
        </div>

        <div className="space-y-2 flex-col flex gap-1">
          <Label htmlFor="signup-confirm-password"> Confirm Password</Label>
          <Input
            id="signup-confirm-password"
            type="password"
            placeholder="********"
            value={confirmPassWord}
            onChange={(e) => setConfirmPassword(e.target.value)}
            className="bg-white/5 border-white/10 placeholder:text-foreground/40 focus:border-white/20 focus:bg-white/20"
          />
        </div>

        <Button
          type="submit"
          disabled={isLoading}
          className="w-full bg-primary hover:bg-primary/90 text-primary-foreground  font-medium transition-all duration-200"
        >
          {isLoading ? "Creating Account" : "Create Account"}
        </Button>
      </form>

      <div className="relative">
        <div className="absolute inset-0 flex items-center">
          <div className="w-full border border-white/10" />
        </div>
        <div className="relative flex justify-center text-xs uppercase">
          <span className="bg-card px-2 text-foreground/60 ">
            {" "}
            Or continue with
          </span>
        </div>
      </div>

      <button onClick={handleGoogleSignIn} className="glow-effect w-full flex items-center  justify-center gap-2 py-2.5 rounded-lg bg-white/5 border  border-white/10   text-foreground/80 transition-color  duration-200 hover:bg-white/10 focus:outline-none focus:ring-2 focus:ring-white/20 focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50">
        <Chrome className="w-4 h-4" />
        <span className="text-sm font-medium"> Continue with Google</span>
      </button>
    </div>
  );
}
``;
